import { Controller, Commands } from '@ali/4ever-cangjie';
import { InsertStickerProps } from '../types';

export default function setSticker(
  controller: Controller,
  payload: InsertStickerProps,
) {
  const { code, node } = payload;
  return node && controller
    .command(Commands.setNodeByKey, node.key, {
      type: node.type,
      data: {
        code,
      },
    })
    .command(Commands.moveToStartOfNextText)
    .command(Commands.focus);
}
